import { Stack, Typography } from '@mui/material'
import { TemplateApi } from 'API/CMS/template'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

export default function DialogPage() {
	const nav = useNavigate()
	const dispatch = useAppDispatch()
	const { id } = useParams<{ id: string }>()
	const template = useAppSelector((s) => s.CMS.Template.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	useEffect(() => {
		if (!open || !template || !template.active) {
			nav(-1)
		}
		return () => {
			setOpen(false)
		}
	}, [nav, open, template])
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => setOpen(false)}
			title="テンプレート削除確認"
			primaryButtonColor="error"
			primaryButtonText="削除する"
			handleSubmit={async () => {
				try {
					if (!id) return
					setLoading(true)
					const { message, error } = await TemplateApi.delete(id)
					dispatch(
						UIAction.addGlobalMessage({
							type: error ? 'error' : 'success',
							message,
						})
					)
					setOpen(false)
				} finally {
					setLoading(false)
				}
			}}
		>
			<Stack>{!!template && <Typography>{`${template.name}を削除してもよろしいですか`}</Typography>}</Stack>
		</DialogConfirm>
	)
}
